  @import '../../../style/App.scss';

  .portfolio__box {
    position: relative;
    max-width: 370px;
    margin: 30px auto;
    cursor: pointer;

    img {
      width: 100%;
      // height: auto;
      height: 400px;
      object-fit: cover;
    }

    .portfolio__hover-info {
      background-color: rgba($main-color, 0.8);
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
      transition: 0.3s ease;

      p {
        color: $white-color;
        padding: 5px 0;

        &.font35 {
          text-transform: uppercase;
        }
      }
    }

    .portfolio__hover-info:hover {
      opacity: 1;
    }
  }